body {
  color: #e0e0e0;
  background-color: #121212;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.soundboard-container {
  padding: 20px;
}

.controls-container {
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
}

.stop-button {
  color: #fff;
  cursor: pointer;
  background-color: #ff4d4d;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

.stop-button.active {
  background-color: #c00;
}

.volume-control {
  align-items: center;
  gap: 10px;
  display: flex;
}

.volume-slider {
  width: 100px;
}

.tabs-container {
  gap: 5px;
  margin-bottom: 20px;
  display: flex;
}

.tab {
  cursor: pointer;
  color: #e0e0e0;
  -webkit-user-select: none;
  user-select: none;
  background-color: #252525;
  border: 1px solid #444;
  border-radius: 4px;
  padding: 8px 12px;
  transition: background-color .3s;
}

.tab.active {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.tab:hover:not(.active) {
  background-color: #404040;
}

.sounds-container {
  flex-wrap: wrap;
  gap: 15px;
  display: flex;
}

.sound-item {
  cursor: pointer;
  color: #e0e0e0;
  background-color: #1e1e1e;
  border: 1px solid #444;
  border-radius: 4px;
  width: 150px;
  padding: 10px;
  transition: background-color .3s;
}

.sound-item:hover {
  background-color: #333;
}

.sound-name {
  margin-bottom: 8px;
  font-weight: bold;
}

.sound-actions {
  gap: 10px;
  display: flex;
}

.favorite-button.active {
  color: #f55;
}

.sidebar-container {
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 30px;
  display: flex;
}

.sidebar-button {
  color: #e0e0e0;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: #252525;
  border: 1px solid #444;
  border-radius: 4px;
  padding: 8px 12px;
  transition: background-color .3s;
}

.sidebar-button:hover {
  background-color: #404040;
}

.sidebar-button.active {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.room-info {
  color: #e0e0e0;
  margin-top: 20px;
  font-weight: bold;
}

