  text-align: center;
  color: #e0e0e0;
  padding: 40px;
}

.room-actions {
  margin-top: 20px;
}

.create-room-button {
  cursor: pointer;
  color: #fff;
  -webkit-user-select: none;
  user-select: none;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.create-room-button:hover {
